@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500&display=swap);

.Navbar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 6vh;
}

.Navbar .logo {
  margin-right: 50px;
  padding: 0 13px;
  font-size: 22px;
  background-color: #eceff1;
  height: 100%;
  display: flex;
  align-items: center;
}

.Navbar .logo a {
  text-decoration: none;
  color: black;
}

.Navbar .slider {
  width: 340px;
  margin: 0 10px;
  display: inline-block;
}

.Navbar .slider .rc-slider-track {
  background-color: transparent;
}

.Navbar .slider .rc-slider-rail {
  height: 8px;
}

.Navbar .slider .rc-slider-handle,
.Navbar .slider .rc-slider-handle:active,
.Navbar .slider .rc-slider-handle:focus,
.Navbar .slider .rc-slider-handle:hover {
  background-color: green;
  outline: none;
  border: 2px solid green;
  box-shadow: none;
  width: 13px;
  height: 13px;
  margin-top: -2px;
}

.Navbar .select-container {
  margin-left: auto;
  margin-right: 1rem;
}

.ColorBox {
  width: 20%;
  height: 25%;
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin-bottom: -3.5px;
}

.ShadesPalette .ColorBox {
  height: 50%;
}

.ColorBox_copy-button,
.ShadesPalette .go-back-button {
  width: 100px;
  height: 30px;
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -15px;
  text-align: center;
  outline: none;
  background: rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  line-height: 30px;
  color: white;
  text-transform: uppercase;
  border: none;
  opacity: 0;
}

.ShadesPalette .ColorBox.go-back {
  background-color: black;
}

.ShadesPalette .ColorBox.go-back .go-back-button {
  opacity: 1;
}

.ColorBox:hover .ColorBox_copy-button {
  opacity: 1;
  transition: 0.5s;
}

.ColorBox_box-content {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 10px;
  color: black;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;
}

.ColorBox_see-more {
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  border: none;
  right: 0;
  bottom: 0;
  color: white;
  width: 60px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
}

.ColorBox_copy-overlay {
  opacity: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  transform: scale(0.1);
  transition: transform 0.6s ease-in-out;
}

.ColorBox_copy-overlay.show {
  opacity: 1;
  transform: scale(50);
  z-index: 10;
  position: absolute;
}

.ColorBox_copy-msg {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 4rem;
  transform: scale(0);
  opacity: 0;
  color: white;
}

.ColorBox_copy-msg.show {
  opacity: 1;
  transform: scale(1);
  z-index: 25;
  transition: transform 0.4s ease-in-out;
  transition-delay: 0.3s;
}

.ColorBox_copy-msg h1 {
  font-weight: 400;
  text-shadow: 1px 2px black;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  text-align: center;
  margin-bottom: 0;
  padding: 1rem;
}

.ColorBox_copy-msg p {
  font-weight: 100;
  font-size: 2rem;
}

.Palette-footer {
  height: 4vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 1rem;
}

.Palette-footer .emoji {
  margin-left: 0.5rem;
}

.Palette {
  height: 100%;
  width: 100%;
}
.Palette-colors {
  display: flex;
  flex-flow: wrap;
  height: 90vh;
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  overflow: hidden;
  font-family: Roboto;
}



